import React from 'react'
import logo from './logo.png'
import {Link} from 'react-router-dom'

const HeaderHome = (props) => {
    return(
        <div className="jumbotron">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6">
                        <h1 className="display-3 text-left" style={{color:'white', fontSize:'10px', marginBottom:'0'}}><Link to="/"><img src={logo} className="App-logo" style={{marginRight:'5px', marginTop:'0'}}alt="logo" width="200" /></Link> beta</h1>
                    </div>
                    <div className="col-lg-6">
                        <p className="text-right" style={{margin:'12px 0 0 0'}}><Link className="btn btn-anuncie btn-lg" to="/novo-anuncio" role="button">Anuncie sua vaga &nbsp;&nbsp;<i className="fa fa-angle-double-right" aria-hidden="true"></i></Link></p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HeaderHome


