import React, {Component} from 'react'

import base from './base'
import AnuncioHome from './AnuncioHome'
import LinkCategoria from './LinkCategoria'
import HeaderHome from './HeaderHome'

class Home extends Component{
    constructor(props){
        super(props)
        this.state = {
          anuncios: []
        }
        
        base.bindToState('anuncios', {
          context: this,
          state: 'anuncios',
          queries: {
            limitToLast: 6
          }
        })
    }
    render(){
        let index = 0
        return(
            <div>
                <HeaderHome />
                <div className="container">
                    <div className="row">                    
                        {this.props.categorias.map((cat,indice) => {
                            return [<LinkCategoria categoria={cat} key={indice} />,++index%4 === 0 && <div key={'c'+indice} className="w-100"></div>]
                        })}
                    </div>
                    <h3 style={{margin:'20px 0 40px 0', color:'#006F83'}}>ltimos Anncios:</h3>
                    <div className="row">
                        {Object.keys(this.state.anuncios).map( key => {
                            const anuncio = this.state.anuncios[key]
                            return <AnuncioHome key={key} id={key} anuncio={anuncio} />
                        })}
                    </div>
                </div>
            </div>
        )
    }
}

export default Home



