import React from 'react'
import {Link} from 'react-router-dom'
import logo from './logo.png'

const HeaderInterno = (props) => {
    return(
        <div className="jumbotron">
          <div className="container">
              <div className="row">
                  <div className="col-lg-6">
                      <h1 className="display-3 text-left" style={{color:'white', fontSize:'10px', marginBottom:'0'}}><Link to="/"><img src={logo} className="App-logo" style={{marginRight:'5px', marginTop:'0'}}alt="logo" width="200" /></Link> beta</h1>
                  </div>
                  <div className="col-lg-6">
                      <p className="text-right" style={{margin:'12px 0 0 0'}}><Link className="btn btn-anuncie btn-lg" to="/regras" role="button"><i className="fa fa-file-text-o" aria-hidden="true"></i> &nbsp;&nbsp;Leia as regras</Link></p>
                  </div>
              </div>
          </div>
        </div>
    )
}

export default HeaderInterno


