import React, {Component} from 'react'
import HeaderInterno from './HeaderInterno'
import base from './base'
import {Redirect} from 'react-router-dom'

class NovoAnuncio extends Component{
    constructor(props){
        super(props)
        this.state ={
            sucess: false,
        }
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    handleSubmit(e){
        const NovoAnuncio = {
            nome: this.nome.value,
            sobre: this.sobre.value,
            endereco: this.endereco.value,
            email:this.email.value,
            faixa:this.faixa.value,
            descricao: this.descricao.value,
            requisitos: this.requisitos.value,
            beneficios: this.beneficios.value,
            categoria: this.categoria.value,
            cidade: this.cidade.value,
            datepost: this.datepost.value
        }
        base.push('anuncios', {
            data: NovoAnuncio
        })
        .then(() => {
            this.setState({sucess: true})
        })

        e.preventDefault()
    }
    render(){
        return(
            <div>
            { this.state.sucess && <Redirect to="/" /> }
                <HeaderInterno />
                <div className="container">
                    <h1 style={{color:'#006F83'}}>Nova vaga</h1>
                    <p style={{marginBottom:'30px'}}>Antes de postar sua vaga, favor ler as regras do site clicando no boto do header "Leia as regras".</p>
                    <form onSubmit={this.handleSubmit} className="cadastro">
                        <div className="row">
                            <div className='form-group col-lg-3'>
                                <input type='text' className="form-control" id="nome" placeholder="Nome da empresa" required ref={(ref) => this.nome = ref}/>
                            </div>
                            <div className='form-group col-lg-3'>
                                <select className="form-control" ref={(ref) => this.categoria = ref}>
                                    {this.props.categorias.map(cat => <option key={cat.url} value={cat.url}>{cat.categoria}</option>)}
                                </select>
                            </div>
                            <div className='form-group col-lg-3'>
                                <input type='text' className="form-control" id="cidade" placeholder="Cidade que est localizada" required ref={(ref) => this.cidade = ref}/>
                            </div>
                            <div className='form-group col-lg-3'>
                                <input type='text' className="form-control" id="sobre" placeholder="Endereo da empresa" required ref={(ref) => this.endereco = ref}/>
                            </div>
                            <div className='form-group col-lg-6'>
                                <textarea className="form-control" id="sobre" placeholder="Sobre a empresa" required ref={(ref) => this.sobre = ref}></textarea>
                            </div>
                            <div className='form-group col-lg-6'>
                                <textarea className="form-control" id="descricao" placeholder="Descrio da vaga" required ref={(ref) => this.descricao = ref}></textarea>
                            </div>
                            <div className='form-group col-lg-4'>
                                <input type='text' className="form-control" id="faixa" placeholder="Faixa salarial" required ref={(ref) => this.faixa = ref}/>
                            </div>
                            <div className='form-group col-lg-4'>
                                <input type='text' className="form-control" id="beneficios" placeholder="Benefcios" required ref={(ref) => this.beneficios = ref}/>
                            </div>
                            <div className='form-group col-lg-4'>
                                <input type='text' className="form-control" id="requisitos" placeholder="Requisitos da vaga" required ref={(ref) => this.requisitos = ref}/>
                            </div>
                            <div className='form-group col-lg-6'>
                                <input type='text' className="form-control" id="datepost" placeholder="Ex: 01/01/18" required ref={(ref) => this.datepost = ref}/>
                                <p style={{marginTop:'10px', fontSize:'12px'}}>Data da postagem do anncio</p>
                            </div>
                            <div className='form-group col-lg-6'>
                                <input type='text' className="form-control" id="email" placeholder="E-mail da seleo" required ref={(ref) => this.email = ref}/>
                            </div>
                            <div className="col-lg-12" style={{margin:'0 0 20px 0'}}>
                                <button type="submit" className="btn btn-primary">Salvar anncio</button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default NovoAnuncio


