import React from 'react'
import HeaderHome from './HeaderHome'
import {Link, Route} from 'react-router-dom'
import Categoria from './Categoria'
import Anuncio from './Anuncio'

const Categorias = (props) => {
    return(
        <div>
            <HeaderHome />
            <div className="container">
                <div className="row">
                    <div className="col-lg-2">
                        <ul className="categoriaspage">
                            {
                                props.categorias.map(
                                    cat => {
                                        return(
                                            <li key={cat.url}>
                                                <Link to={`/categorias/${cat.url}`} style={{marginBottom:'10px'}} className="btn btn-primary btn-xs"><i className="fa fa-long-arrow-right" aria-hidden="true"></i> {cat.categoria}</Link>
                                            </li>
                                        )
                                    }
                                )
                            }
                        </ul>
                    </div>
                    <div className="col-lg-10">
                        <Route path="/categorias/:urlCategoria" exact component={Categoria} />
                        <Route path='/categorias/:urlCategoria/:idAnuncio' render={(props) => <Anuncio {...props} />} />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Categorias


