import React from 'react'
import {Link} from 'react-router-dom'

const AnuncioHome = ({id, anuncio}) =>{
    return(
        <div className="col-lg-6 col-md-6 mb-6" style={{marginBottom:'30px'}}>
          <div className="anuncio bgarticle">
              <span className="label label-info">{anuncio.categoria}</span>
              <div className="card-body">
                <h4 className="card-title">
                  <Link to={`/categorias/${anuncio.categoria}/${id}`}>{anuncio.nome}</Link>
                </h4>
                <h5>{anuncio.nome}</h5>
                <p className="card-text readmore">{anuncio.descricao}</p>
              </div>
          </div>
        </div>
    )
}

export default AnuncioHome


