import React from 'react'
import {Link} from 'react-router-dom'

const Footer = (props) =>{
    return(
        <footer className="py-5 mt-4" style={{backgroundColor:'#006F83'}}>
        <div className="container">
          <p className="m-0 text-center" style={{color:'white'}}>Feito com <i className="fa fa-heart" aria-hidden="true" style={{color:'white'}}></i> por: Jlio Carneiro. Ateno, antes de postar <Link to="/regras" style={{color:'white'}}>leia as regras</Link>.</p>
        </div>
        </footer>
    )
}

export default Footer


