import React from 'react'
import HeaderHome from './HeaderHome'

const Regras = (props) => {
    return(
        <div>
            <HeaderHome />
            <div className="container">
                <div className="row">
                    <div className="col-lg-2">
                    <h1 style={{color:'#006F83'}}>Regras</h1>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Regras


