import React, {Component} from 'react'
import axios from 'axios'

class Anuncio extends Component{
    constructor(props){
        super(props)

        this.state = {
            anuncio: {},
            isLoading: true
        }

        const id = this.props.match.params.idAnuncio
        const url = `https://mercadodev-77f8c.firebaseio.com/anuncios/${id}.json`
        axios
        .get(url)
        .then(data => {
            this.setState({anuncio: data.data, isLoading: false})
        })
    }
    render(){
        const anuncio = this.state.anuncio

        if(this.state.isLoading){
            return <p className="text-center" style={{margin:'10% 0'}}><i className="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i></p>
        }

        return(
            <div>
                <h2>{anuncio.nome}</h2>
                <p>Esta vaga se aplica a <span className="label label-info">{anuncio.categoria}</span> em <span className="label label-info">{anuncio.cidade}</span>. A vaga foi postada em {anuncio.datepost}</p>
                <p><b>Sobre a empresa:</b> A empresa est localizada em "{anuncio.endereco} - {anuncio.cidade}". {anuncio.sobre}</p>
                <p><b>Sobre a vaga:</b> {anuncio.descricao}</p>
                <p><b>Requisitos:</b> {anuncio.requisitos}</p>
                <p><b>Faixa salarial:</b> {anuncio.faixa}</p>
                <p><b>Benefcios:</b> {anuncio.beneficios}</p>
                <p><b>Email de contato:</b> {anuncio.email}</p>
            </div>
        )
    }
}

export default Anuncio


