import React from 'react'
import {Link} from 'react-router-dom'

const LinkCategoria = ({categoria}) =>{
    return(
        <Link to={`/categorias/${categoria.url}`} className="btn category col-sm">
            <i className={`fa ${categoria.icon}`} aria-hidden="true" style={{fontSize:'100px'}}></i><br />
            {categoria.categoria}
        </Link>
    )
}

export default LinkCategoria


